*{
    font-family: arboria, sans-serif;
}


.slideEm{
    margin-left: 5px;
}

.container-opaque {
    border-radius: 10px;
    width: 85%;
    margin-top: 30px;
    background-color: #f1f2f3;
    padding-bottom: 20px;
    padding-top: 20px;
}

.pinkBttn {
    background-color: #fed0ff;
    border-radius: 6px;
}

.initials {
    font-weight: 700;
    font-size: 25px;
    padding: 0px 15px;
}

.customPadding {
    margin-top: 16px;
}

.btn-primary {
    background-color: #fc99cf;
    border-color: #fc99cf;
}

.btn-primary:hover {
    background-color: #fe68cf;
    border-color: #fe68cf;
}

.btn-primary.focus, .btn-primary:focus {
    background-color: #cf9bfb;
    border-color: #cf9bfb;
    box-shadow: 0 0 0 0.2rem rgba(252,153,207,.5);
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
    background-color: #cf9bfb;
    border-color: #cf9bfb;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(252,153,207,.5);
}

#profilePic {
    width: 100%;
    height: 100%;
    max-width: 400px;
    max-height: 400px;
    border-radius: 10px;
    float: left;
    margin-right: 15px;
    margin-bottom: 15px;
}

.customPadding2{
    margin-bottom: 20px;
}

